import React, { useState, useEffect } from 'react';
import getKey from '../../utils/KeyGenerator';
import golfScoring from './golfScoring';
import parsDefault from './parsDefault';
import ScoreCardGolf from './ScoreCardGolf';
import initializeData from '../../utils/InitializeData';

const GolfScores = ({ playerIndex, editPlayer, deletePlayer, updateScores }) => {
    
    const [golfPars, setGolfPars] = useState(initializeData('golfPars', parsDefault));

    useEffect(() => {
        localStorage.setItem('golfPars', JSON.stringify(golfPars));
    }, [golfPars]);

    const editGolfPar = (hole) => {
        const newPars = [...golfPars];
        const newPar = prompt(`Enter par for hole ${hole}:`, golfPars[hole]);
        newPars[hole] = newPar;
        setGolfPars(newPars);
    }
    const golfScores = () => golfScoring.map((hole, index) => <div key={getKey(hole)} className='scrollSnapTop pt-1'>
        <div className='contentCenter bg-darker r-10 m-5 mt-35'>
            <div>
                <span className='copyright greet color-yellow'>
                    Hole:
                </span>
                <span className='size40 bold pt-5 pl-15 pr-15 bg-darker r-10 color-white'>
                    {index + 1}
                </span>
                <span className='copyright greet color-yellow'>
                    Par:
                </span>
                <span className='size20 bold color-lite mt--5'>
                    {golfPars[index]}
                </span>
            </div>
            <ScoreCardGolf
                playerIndex={playerIndex}
                scoreIndex={index}
                editPlayer={editPlayer}
                deletePlayer={deletePlayer}
                updateScores={updateScores}
            />
        </div>
    </div>);

    return golfScores();
}

export default GolfScores;