import React, { useState, useEffect, useContext } from 'react';
import { PlayerContext } from '../../context/PlayerContext';
import getKey from '../../utils/KeyGenerator';
import GetAthleteScore from './GetAthleteScore';

const ScoreBoard = ({
    scoreboardCollapse,
    addNewScore,
    timesUp
}) => {

    const {
        players,
        setPlayers
    } = useContext(PlayerContext);

    return (
        !scoreboardCollapse ? (
            <div className='containerBox flexContainer h-scroll'>
                {
                    players.map((player, index) => <div
                        key={getKey(`SurfAthlete${index}`)}
                        className={` button flex${players.length}Column`}
                        onClick={() => addNewScore(index)}
                    >
                        <GetAthleteScore
                            index={index}
                            timesUp={timesUp}
                        />
                    </div>
                    )
                }
            </div>
        ) : (
            <div>
                {/* Add the content you want to render when scoreboardCollapse is true */}
            </div>
        )
    );
}
export default ScoreBoard;